#Cart {
  width: 350px;
  height: 100vh;
  position: fixed;
  right: -350px;
  top: 0;
  z-index: 2;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.3s ease-in-out;
}
#Cart.opened {
  right: 0;
}
.cart-overlay {
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
}
.cart-overlay.opened {
  display: block;
}
.cart-content {
  width: 100%;
  padding: 20px;
  overflow-y: scroll;
}
.cart-content::-webkit-scrollbar {
  width: 13px;
}
.cart-content::-webkit-scrollbar-track {
  background-color: transparent;
  margin: 10px;
}
.cart-content::-webkit-scrollbar-thumb {
  border: 4px solid #fff;
  background-clip: padding-box;
  border-radius: 10px;
  background: #ffbe33;
}
.cart-content .cart-header,
.cart-content .cart-body {
  width: 100%;
}
.cart-content .cart-header {
  display: flex;
  margin-bottom: 20px;
  align-items: center;
  justify-content: space-between;
}
.cart-content .cart-header > h4 {
  text-transform: capitalize;
}
.cart-content .cart-body .items-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.checkout {
  padding: 20px;
  text-align: end;
  position: relative;
  border-top: 1px solid #ffbe33;
}
.checkout .checkout-content {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.checkout .checkout-content .total h4,
.checkout .checkout-content .total h5 {
  text-transform: capitalize;
  text-align: start;
}
.checkout .checkout-content .total h4 {
  font-size: 14px;
}
.checkout .checkout-content .total h5 {
  font-size: 12px;
  color: #ffbe33;
}
.checkout .checkout-content > h4 {
  font-size: 18px;
}
.checkout .checkout-buttons {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 10px;
}
.checkout .checkout-buttons > a {
  margin: 0;
  padding: 5px 20px;
  font-size: 14px;
}
